import React, { Suspense } from 'react';
import SubmitDataHelpers from './components//helpers/SubmitDataHelpers';
import CookieHelpers from './components/helpers/CookieHelpers';
import Main from './components/Main';
import AppStyle from './css/AppStyle';
import './App.css';

class App extends React.Component {
  constructor(props){
    super(props);
    this.state = {};
  }
  componentDidMount(){
    this.authenticate();
    this.renderHeader();
    AppStyle();
  }

  componentDidUpdate(){
    AppStyle();
  }

  render(){
      const Component = this.state.component;
      let render = this.state.render;
      
      return (
        <div id="app" className="App container-fluid shadow-lg">
          <Suspense fallback={''}>
              {Component && render? <Component/> : ''}
              {Component && render? <Main/> : ''}
          </Suspense>   
        </div>
      );
  }

  renderHeader(){
    let path;

    switch(window.location.pathname){
      case '/':
      case '/login':
      case '/register':
      case '/guest':
        path = './components/Layout/Header';
        break;
      default:
        path = './components/Layout/Nav';
        break;
    }

    const Component = React.lazy(() => import(`${path}`));

    this.setState({...this.state, ['component']: Component});
  }

  authenticate(){
    let helpers = new SubmitDataHelpers;

    helpers.postData('/authentication/loggedin/',{})
    .then(res => {
      console.log(res);
      
      if(!res){
        console.log("no res");
        return false;
      }

      if(!res.error && window.location.hostname === "localhost"){
        let now = Date.now();
        let expiredDate = new Date(now + (86400000 * 14));
        expiredDate = expiredDate.toUTCString();
        
        let cookieHelpers = new CookieHelpers;
        cookieHelpers.setCookie("username", res.username, expiredDate, "/");
        cookieHelpers.setCookie("activeId", res.activeId, expiredDate, "/");
      }

      if(!res.error){
        //console.log(res); 
        switch(window.location.pathname){
          case '/':
          case '/home':
          case '/login':
          case '/register':
          case '/guest':
            window.location.href = "/events";
          default:
            this.setState({render: true});
            break;
        }
      } else if(res.error == "not logged in" && window.location.pathname !== '/'){
        let pathname = window.location.pathname;
        let isAuth = pathname === '/login' 
                    || pathname === '/register' 
                    || pathname.includes('/guest')
                    || pathname === '/logout';
        if(!isAuth){
          window.location.href = "/";
        } else {
          this.setState({render: true});
        }
      } else {
        this.setState({render: true});
      }
    });
  }
}

export default App;