export {eId, eClass, darkLightMode, dragButton, randomColor};

let eId = function getId(ref){
    return document.getElementById(ref);
}

let eClass = function getClass(ref){
    return document.getElementsByClassName(ref);
};

let darkTheme = function setDarkThemeByChangeClassName(ref){
    let elements = document.getElementsByClassName(ref);
    
    for(let i = 0; i < elements.length; i++){
        elements[i].className = elements[i].className.replace(" dark-" + ref, '') + " dark-" + ref;
    }
}

let lightTheme = function setLightThemeByChangeClassName(ref){
    let elements = document.getElementsByClassName(ref);
    
    for(let i = 0; i < elements.length; i++){
        elements[i].className = elements[i].className.replace(" dark-" + ref, '');
    }
}

let darkLightMode = function setDarkLightMode(ref){
    let darkThemeBtn = document.getElementById('darkThemeSwitch');

    if(window.localStorage.getItem("darkThemeMode") == "on"){
        darkThemeBtn.checked = true;
        document.body.style.backgroundColor = "rgba(0,0,0,0.7)";
        darkTheme(ref);
    } else {
        darkThemeBtn.checked = false;
    }

    darkThemeBtn.addEventListener('click', function(){
        let darkThemeMode = darkThemeBtn.checked;

        if(darkThemeMode){
            document.body.style.backgroundColor = "rgba(0,0,0,0.7)";
            darkTheme(ref);
        } else {
            document.body.style.backgroundColor = "rgba(0,0,0,0)";
            lightTheme(ref);
        }
    });
}

function dragButton(ref){
	//Make the DIV element draggagle:
	dragElement(document.getElementById(ref));
	
	function dragElement(elmnt) {
        var pos1 = 0, pos2 = 0;
        
		elmnt.onmousedown = dragMouseDown;

		
		function dragMouseDown(e) {
			e = e || window.event;
			e.preventDefault();
			// get the mouse cursor position at startup:
			pos1 = e.clientY;
			document.onmouseup = closeDragElement;
			// call a function whenever the cursor moves:
			document.onmousemove = elementDrag;
		}
		
		function elementDrag(e) {
			e = e || window.event;
			e.preventDefault();
			// calculate the new cursor position:
			pos2 = pos1 - e.clientY;
			pos1 = e.clientY;
			// set the element's new position:
			elmnt.style.top = (elmnt.offsetTop - pos2) + "px";
			elmnt.style.right = 0 + "px";
		}
		
		function closeDragElement() {
			/* stop moving when mouse button is released:*/
			document.onmouseup = null;
			document.onmousemove = null;
		}
	}
}

function randomColor(opacity=1){
    return 'rgba('
        + Math.round((Math.random())*255)+','
		+ Math.round((Math.random())*255) + ','
        + Math.round((Math.random())*255) + ','
        + opacity
        + ')';
}