import {eId, eClass, darkLightMode} from '../../components/helpers/StylingHelpers';

function MainStyle(){
    setDarkLightMode();
    setStyle();

    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    if (!(/android/i.test(userAgent))) {
        window.addEventListener('resize', setStyle);
    }
}

export default MainStyle;

function setStyle(){
    let uri = ['/', '/login', '/register', '/guest'];

    if(uri.indexOf(window.location.pathname) < 0){
        eId("main").style.margin = 0;
    } else {
        if(window.innerWidth > window.innerHeight){
            eId("main").className = eId("main").className.replace(" mb-3", "") + " mb-3";
        } else {
            eId("main").className = eId("main").className.replace(" mb-2","") + " mb-2";
        }
    }

    setInput('input');
    setBtn('btn');
}

function setDarkLightMode(){
    darkLightMode('main');
    darkLightMode('input');
    darkLightMode('button');
}

function setInput(ref){
    let btn = eClass(ref);
    for(let i = 0; i < btn.length; i++){
        btn[i].style.borderRadius = btn[0].offsetWidth * 0.03 + 'px';
    }
}

function setBtn(ref){
    let btn = eClass(ref);
    for(let i = 0; i < btn.length; i++){
        btn[i].style.borderRadius = btn[0].offsetWidth * 0.15 + 'px';
    }
}
