import {eId, eClass, darkLightMode} from '../components/helpers/StylingHelpers';

function AppStyle(){
    darkLightMode("App");
    darkLightMode("shadow");
    darkLightMode("shadow-lg");
    setStyle();

    var userAgent = navigator.userAgent || navigator.vendor || window.opera;
    if (!(/android/i.test(userAgent))) {
        window.addEventListener('resize', setStyle);
    }
}

function setStyle(){
    let uri = ['/', '/login', '/register', '/guest'];

    if(uri.indexOf(window.location.pathname) >= 0){
        eId("app").style.minHeight = eId('app').offsetWidth * 0.6 + 'px';

        if(window.innerWidth > window.innerHeight){
            eId('app').style.borderRadius = parseInt(eId('app').offsetHeight) * 0.07 + 'px';
        } else {
            eId('app').style.borderRadius = parseInt(eId('app').offsetWidth) * 0.07 + 'px';
        }
    } else {
        eId("app").style.minHeight = window.innerHeight + 'px';
        eId('app').style.position = 'relative';
        eId("app").style.borderRadius = 0;
        eId("app").className = eId("app").className.replace(" pt-5", "");
    }
}

export default AppStyle;