class CookieHelpers{
    constructor(){
        this.cookies = document.cookie;
    }
}

export default CookieHelpers;

CookieHelpers.prototype.getCookie = function getCookie(ref){
    let i = this.cookies.indexOf(ref);
    let j = this.cookies.indexOf('=', i);
    let k = this.cookies.indexOf(';', i);
    
    if(k > 0){
        return this.cookies.substring(j+1, k);
    } else {
        return this.cookies.substring(j+1);
    }

    return false;
}

CookieHelpers.prototype.setCookie = function setCookie(name, val, expiredDate, path){
    document.cookie = name + "=" + val + "; expires=" + expiredDate + "; path=" + path;
}