class SubmitDataHelpers{
}

export default SubmitDataHelpers;

//Set data to send login information to server
SubmitDataHelpers.prototype.postData = async function postData(uri = '', data = {}){
    const response = await fetch('http://orienteering2020.000webhostapp.com' + uri, {
        method: 'POST',
        mode: 'cors',
        cache: 'no-cache',
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
        },
        credentials: 'include',
        redirect: 'follow',
        referrerPolicy: 'no-referrer',
        body: JSON.stringify(data),
    });

    /*if(uri == '/authentication/loggedin/'){
        return await response.text();
    }*/

    return await response.json();  
}

SubmitDataHelpers.prototype.getText = async function getText(uri = ''){
    const response = await fetch('http://localhost:3000' + uri,);

    return await response.text();  
}