import React, {Suspense} from 'react';
import MainStyle from '../css/Layout/MainStyle';
import '../css/Layout/Main.css';

class Main extends React.Component {
  constructor(props){
    super(props);
    this.state = {};
  }

  componentDidMount(){
    this.renderMain();
    MainStyle();
  }

  componentDidUpdate(){
    MainStyle();
  }

  render(){
    const Component = this.state.component;

    return (        
      <div id="main" className="main">
        <Suspense fallback={''}>
            {Component? <Component/>: ''}
        </Suspense>
        
      </div>
    );
  }

  renderMain(){
    let path;

    switch(window.location.pathname){
      case '/':
        path = './authentication/Home';
        break;
      case '/login':
        path = './authentication/Login';
        break;
      case '/register':
        path = './authentication/Register';
        break;
      case '/guest':
        path = './authentication/Guest';
        break;
      case '/guest/scan':
        path = './guest/Scan';
        break;
      case '/guest/result':
        path = './guest/Result';
        break;
      case '/events':
        path = './events/EventList';
        break;
      case '/events/new':
        path = './events/CreateEvent';
        break;
      case ("/events/" + encodeURI(window.sessionStorage.getItem("eventName"))):
        path = './events/SingleEvent';
        break;
      case '/courses/new':
        path = './courses/CreateCourse';
        break;
      case (
        "/events/" 
        + encodeURI(window.sessionStorage.getItem("eventName"))
        + "/" 
        + window.sessionStorage.getItem("courseType")
        + "/"
        + window.sessionStorage.getItem("courseAccessKey")
      ):
        path = './courses/SingleCourse';
        break;
      default:
        path = './events/EventList';
        break;
    }

    const Component = React.lazy(() => import(`${path}`));

    this.setState({...this.state, ['component']: Component});
  }
}

export default Main;